@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.wrapper {
  min-height: 100px;
  position: relative;
  pointer-events: none;
}

.loaderContainer {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  bottom: 0;
  content: '';
  cursor: wait;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 10;
}

.artist-info-card {
  width: 45%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10% ;
  padding: 5%;
  border: 1px solid black;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.artist-info-card-header {
  font: 40px;
  margin-bottom: 15px;
}

.artist-info-card-subtitle {
  display: flex;
  flex-direction: row;
  margin-bottom: 15px;
}

.artist-list {
  margin-top: 30px;
}
.artist-link {
  text-align: center;
}

.artist-link-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 5px;
  margin-top: 20px;
}
.bio {
  text-align: left;
}
.bio-title {
  text-align: left;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
  letter-spacing: 3px;
  margin-bottom: -15px;
  padding-bottom: 0px;
}
.header-container {
  text-align: center;
  margin-bottom: 30px;
}
.home-container {
  border: 1px solid #14213d;
  padding: 5%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: white;
}

.inline {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  border-bottom: .5px solid black;
  margin-bottom: 10px;
}

.item-list {
  margin-top: 20px;
  width: 45%;
  border: 1px solid #14213d;
  padding: 5%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-left: auto;
  margin-right: auto;
}
.list {
  list-style: none;
}

.search-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.submit-button {
  margin-top: 20px;
}

.subtitle-item {
  width: 100%;
  text-align: center;
  margin: 0;

}

.main-container {
  height: 100%;
  width: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e5e5e5;
}

#root {
  height: 100%;
  width: 100%;
  margin: 0;
}

